/* STYLESHEET */
/* Basic structure */
:root {
  font-size: 15px;
}

@media (max-width: 599px) {
  :root {
    font-size: 2.5vw;
  }
}
@media (max-width: 479px) {
  :root {
    font-size: 12px;
  }
}
body {
  background-color: rgb(44, 44, 44);
  background-size: 100% auto;
  background-repeat: repeat-y;
  background-attachment: fixed;
  background-position: center;
}

:root.no-webp body {
  background-image: url(../img/background-3840.jpg);
}
@media (max-width: 1920px) {
  :root.no-webp body {
    background-image: url(../img/background-1920.jpg);
  }
}
:root.webp body {
  background-image: url(../img/background-3840.webp);
}
@media (max-width: 1920px) {
  :root.webp body {
    background-image: url(../img/background-1920.webp);
  }
}

.main {
  display: flex;
  flex-flow: column nowrap;
  position: relative;
  margin: 0 auto;
  width: 40%;
  min-width: 600px;
  min-height: 100vh;
  font-size: 1rem;
}

@media (max-width: 599px) {
  .main {
    width: 100%;
    min-width: 100%;
  }
}
body::-webkit-scrollbar {
  width: 12px;
  height: 12px;
}

body::-webkit-scrollbar-track {
  border-radius: 6px;
  background: rgb(128, 128, 128);
}

body::-webkit-scrollbar-thumb {
  border-radius: 6px;
  background: rgb(80, 80, 80);
}
body::-webkit-scrollbar-thumb:hover {
  background: rgb(48, 48, 48);
}
body::-webkit-scrollbar-thumb:active {
  background: rgb(32, 32, 32);
}

:root, body {
  scrollbar-width: auto;
  scrollbar-color: rgb(128, 128, 128) rgb(80, 80, 80);
}

:focus-visible {
  outline: 1px auto rgb(34, 34, 34);
}

.focus-visible {
  outline: 1px auto rgb(34, 34, 34);
}

[tabindex="-1"]:focus-visible {
  outline: none;
}

[tabindex="-1"].focus-visible {
  outline: none;
}

.no-break {
  white-space: nowrap;
}

/* Navigation */
#navbar_container {
  position: fixed;
  top: 0;
  left: 0;
  width: 30%;
  height: 100vh;
  z-index: 11;
  overflow: hidden;
}
@media (max-width: 1500px) {
  #navbar_container {
    width: calc(50% - 300px);
  }
}

#navbar {
  position: absolute;
  top: 0;
  right: 0;
  box-sizing: border-box;
  padding: 0 1px;
  width: 15px;
  height: 100vh;
  background-color: rgb(96, 56, 56);
  transition: 0.3s;
  transition-property: width, opacity;
}
#navbar:empty {
  display: none;
}
#navbar.show {
  width: 40px;
}

#navbar_contents {
  width: 100%;
  height: 100%;
  display: grid;
  grid-row-gap: 1px;
  grid-template-columns: 100%;
  font-size: 1.4em;
  font-size: min(max(1.1em, 2.4vh), 1.4em);
  pointer-events: none;
  transition: inherit;
  transition-property: none;
  overflow-y: scroll;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
#navbar_contents::-webkit-scrollbar {
  display: none;
}
#navbar.show #navbar_contents {
  pointer-events: auto;
}

@media (max-width: 680px) {
  #navbar_container {
    width: 40px;
    visibility: hidden;
  }
  #navbar {
    left: 0;
    right: auto;
    visibility: visible;
    opacity: 0;
    transition-property: width, opacity;
  }
  #navbar.show {
    opacity: 1;
  }
}
#navbar .navitem {
  margin: 0;
  border: none;
  padding: 0.1em 0;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  outline: none;
  background-color: rgb(64, 32, 48);
  cursor: pointer;
  font-family: Arial, sans-serif;
  transition: inherit;
  transition-property: none;
  user-select: none;
}
#navbar .navitem.current {
  background-color: rgb(80, 32, 160);
}

.navtext {
  width: 100%;
  text-align: center;
  white-space: nowrap;
  overflow: hidden;
  opacity: 0;
  transition: inherit;
  transition-property: opacity;
  color: rgb(255, 128, 128);
}
#navbar.show .navtext {
  opacity: 1;
}
.navitem:hover > .navtext, .navitem.focus-visible > .navtext {
  color: rgb(255, 255, 255);
}
@media (hover: none), (pointer: coarse) {
  .navitem:hover > .navtext {
    color: rgb(255, 128, 128);
  }
  .navitem:active > .navtext {
    color: rgb(255, 255, 255);
  }
}

.navicon {
  width: 100%;
  height: 1em;
  background-repeat: no-repeat;
  background-position: center 0;
  background-size: auto 3em;
  opacity: 0;
  transition: inherit;
  transition-property: opacity;
  background-position-y: 0;
}
#navbar.show .navicon {
  opacity: 1;
}
.navitem:hover > .navicon, .navitem.focus-visible > .navicon {
  background-position-y: -2em;
}
@media (hover: none), (pointer: coarse) {
  .navitem:hover > .navicon {
    background-position-y: 0;
  }
  .navitem:active > .navicon {
    background-position-y: -2em;
  }
}

#icon_info {
  background-image: url(../img/icons/info.svg);
}

#icon_manual {
  background-image: url(../img/icons/manual.svg);
}

#icon_speculation {
  background-image: url(../img/icons/lightbulb.svg);
}

#icon_book {
  background-image: url(../img/icons/book.svg);
}

/* Menu */
#menu_container {
  position: fixed;
  top: 0;
  right: 0;
  width: 30%;
  height: 100vh;
  z-index: 10;
  overflow: hidden;
}
@media (max-width: 1500px) {
  #menu_container {
    width: calc(50% - 300px);
  }
}

#menu {
  position: absolute;
  top: 0;
  left: 0;
  width: calc(100% - 40px);
  max-width: 28rem;
  min-width: 25rem;
  height: 100vh;
  transform: translate(-100%, 0);
  background-color: rgb(93, 45, 61);
  transition: 0.6s;
  transition-property: transform;
}
#menu_container.show #menu {
  transform: none;
}

#menu_button_wrapper {
  position: absolute;
  box-sizing: border-box;
  left: 100%;
  width: 15px;
  height: 100%;
  overflow: hidden;
  padding: 0 1px;
  background-color: rgb(93, 45, 61);
  transition: 0.3s;
  transition-property: width;
}
#menu_button_wrapper.show, #menu_container.show #menu_button_wrapper {
  width: 40px;
}
#menu_container.show #menu_button_wrapper {
  padding: 0;
}

#menu_button {
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  background-color: rgb(80, 32, 48);
  cursor: pointer;
  outline: none;
  pointer-events: none;
}
#menu_button_wrapper.show #menu_button, #menu_container.show #menu_button {
  pointer-events: auto;
}
#menu_button > div {
  position: absolute;
  left: 20%;
  width: 60%;
  height: 4px;
  border-radius: 2px;
  background-color: rgb(255, 128, 128);
  visibility: hidden;
  transition: 0.6s;
  transition-property: opacity, transform;
}
#menu_button_wrapper.show #menu_button > div {
  background-color: rgb(255, 255, 255);
}
#menu_button_wrapper.show #menu_button > div, #menu_container.show #menu_button > div {
  visibility: visible;
}
#menu_button > div:nth-child(1) {
  bottom: calc(50% + 8px);
}
#menu_container.show #menu_button > div:nth-child(1) {
  transform: translate(0, 10px) rotate(45deg);
}
#menu_button > div:nth-child(2) {
  top: calc(50% - 2px);
}
#menu_container.show #menu_button > div:nth-child(2) {
  opacity: 0;
}
#menu_button > div:nth-child(3) {
  top: calc(50% + 8px);
}
#menu_container.show #menu_button > div:nth-child(3) {
  transform: translate(0, -10px) rotate(-45deg);
}

#menu_content_wrapper {
  width: 100%;
  height: 100%;
  overflow: hidden;
}

#menu_content {
  position: relative;
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  padding: 10px 0;
  border-left: 15px solid rgb(80, 32, 48);
  visibility: hidden;
  user-select: none;
  background-color: rgb(93, 45, 61);
  color: rgb(255, 128, 128);
  font-size: 1.4em;
  font-family: Arial, sans-serif;
  transition: 0.6s;
  transition-property: visibility;
  overflow-y: scroll;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
#menu_content::-webkit-scrollbar {
  display: none;
}
#menu_container.show #menu_content {
  visibility: visible;
}

@media (max-width: 1442px) {
  #menu_container {
    overflow: visible;
    visibility: hidden;
  }
  #menu {
    left: auto;
    right: 100%;
    transform: none;
    background: transparent;
    overflow: visible;
    visibility: hidden;
    transition-property: right;
  }
  #menu_container.show #menu {
    right: 40px;
  }
  #menu_button_wrapper {
    visibility: visible;
  }
  #menu_content {
    transform: translate(100%, 0);
    transition: 0.6s;
    transition-property: transform, visibility;
  }
  #menu_container.show #menu_content {
    transform: none;
  }
}
@media (max-width: 680px) {
  #menu_container {
    position: fixed;
    top: 0;
    right: 0;
  }
  #menu {
    right: 40px;
    transition-property: none;
  }
  #menu_button_wrapper {
    width: 40px;
    transform: translate(25px, 0);
    opacity: 0;
    transition-property: transform, opacity;
  }
  #menu_button_wrapper.show, #menu_container.show #menu_button_wrapper {
    transform: none;
    opacity: 1;
  }
}
@media (max-width: 340px) {
  #menu {
    width: calc(100vw - 40px);
    min-width: calc(100vw - 40px);
    max-width: calc(100vw - 40px);
  }
}
.menu-item {
  display: block;
  position: relative;
  padding: 0.3rem 1.4rem;
  border: none;
  outline: none;
  text-align: left;
  background: transparent;
  color: rgb(255, 128, 128);
}
.menu-item:hover, .menu-item.focus-visible-within {
  background-color: rgb(128, 64, 80);
}
.menu-item.interactive {
  cursor: pointer;
}
.menu-item.interactive:hover, .menu-item.interactive.focus-visible {
  color: rgb(255, 255, 255);
}
@media (hover: none), (pointer: coarse) {
  .menu-item.interactive:hover {
    color: rgb(255, 128, 128);
  }
  .menu-item.interactive:active {
    color: rgb(255, 255, 255);
  }
}

.submenu > .submenu-content {
  font-size: 1em;
}
.submenu > .submenu-content .menu-item {
  padding-left: 1.4rem;
}
.submenu .submenu > .submenu-content {
  font-size: 0.85em;
}
.submenu .submenu > .submenu-content .menu-item {
  padding-left: 2.8rem;
}
.submenu .submenu .submenu > .submenu-content {
  font-size: 0.9em;
}
.submenu .submenu .submenu > .submenu-content .menu-item {
  padding-left: 4.2rem;
}

.submenu-main > .menu-item {
  padding-top: 0.4em;
  padding-bottom: 0.4em;
  font-size: 1.2em;
  font-weight: bold;
}

.menu-item-button {
  position: absolute;
  margin: 0;
  padding: 2px 2px;
  border: none;
  border-radius: 0.7rem;
  outline: none;
  top: 50%;
  transform: translate(0, -50%);
  width: 5rem;
  height: 2em;
  text-align: center;
  font-size: 1rem;
  background-color: #6f6fd0;
  background-image: linear-gradient(to bottom, rgb(136, 136, 255), rgb(85, 85, 160));
  color: rgb(255, 255, 255);
  cursor: pointer;
  opacity: 0;
}
.menu-item-button:nth-last-child(1) {
  right: 0.3rem;
}
.menu-item-button:nth-last-child(2) {
  right: 5.6rem;
}
.menu-item:hover > .menu-item-button, .menu-item.focus-visible-within > .menu-item-button {
  opacity: 0.5;
}
.menu-item > .menu-item-button:hover, .menu-item > .menu-item-button.focus-visible {
  opacity: 1;
}
.menu-item-button:active {
  background-color: rgb(136, 136, 255);
  background-image: linear-gradient(to bottom, rgb(85, 85, 160), rgb(136, 136, 255));
}
@media (hover: none), (pointer: coarse) {
  .menu-item > .menu-item-button:hover {
    opacity: 0.5;
  }
  .menu-item > .menu-item-button:active {
    opacity: 1;
  }
}

#menu input[type=number] {
  background-color: rgb(80, 32, 48);
  color: rgb(255, 128, 128);
  border: 1px solid currentColor;
  outline: none;
}
#menu input[type=number]:focus {
  color: rgb(255, 255, 255);
}

#spoiler_episode_limit {
  width: 2.5em;
}

#menu a[href] {
  text-decoration: none;
  outline: none;
}

/* Header */
.header {
  padding: 0 1.5rem;
  background-color: rgba(64, 64, 64, 0.9);
}

.title {
  margin: 0;
  padding: 0.8em 0;
  text-align: center;
  font: bold italic 2.2em/1.4 Times, serif;
  color: rgb(255, 96, 96);
}

/* Content */
.content {
  flex-grow: 1;
  background-color: rgb(34, 34, 34);
  text-align: justify;
  hyphens: auto;
  font: 1em/1.4 Arial, sans-serif;
}

h2 {
  margin: 0;
  padding: 0;
  font-size: 1.5em;
  font-weight: bold;
}

h3 {
  font-size: 1.2em;
  font-weight: bold;
}

h4 {
  font-size: 1em;
  font-weight: bold;
}

h5 {
  font-size: 1em;
  font-weight: bold;
}

.panel-major > .panel-heading {
  padding: 0.25em 1.2rem;
  border: none;
  text-align: left;
  font-size: 1.5em;
  background-color: rgb(0, 0, 85);
}
.panel-major > .panel-heading > * {
  font-size: 1em;
}
.panel-minor > .panel-heading {
  padding: 0;
  border: none;
  text-align: left;
  background: transparent;
}

.panel-major > .panel-body {
  padding: 1.2rem 3.6rem;
  color: rgb(224, 224, 224);
}
.panel-body div, .panel-body p, .panel-body ol, .panel-body ul {
  margin: 0.6rem 0;
}
.panel-body h3 {
  margin: 1.2rem 0 1.2rem 0;
}
.panel-body h4 {
  margin: 1.1rem 0 0.6rem 0;
}
.panel-body li {
  margin: 0.3rem 0;
}
.panel-body li > h4 {
  margin-bottom: 0.2rem;
}
.panel-body li > h4 + p {
  margin-top: 0.2rem;
}
.panel-body li > p.highlight {
  margin-top: 0.6rem;
  margin-bottom: 0.6rem;
}
.panel-body li > ul:first-of-type {
  margin-top: 0.3rem;
}
.panel-body li > .collapsible:not(.show):last-child:not(:first-child) {
  margin-bottom: 0.6rem;
}
.panel-body *:first-child {
  margin-top: 0;
}
.panel-body *:last-child {
  margin-bottom: 0;
}
.panel-body .highlight {
  text-align: center;
  font-weight: bold;
}

em {
  font-style: italic;
}

.strong {
  font-weight: bold;
}

.term, .thought, .othertitle {
  font-style: italic;
}

.line {
  display: inline-block;
}

.main a[href],
.main a[data-goto],
.main .text-button {
  margin: 0;
  padding: 0;
  border: none;
  background: transparent;
  text-decoration: underline;
  outline-offset: 0.1em;
  cursor: pointer;
}
.main a[href]:hover, .main a[href]:active, .main a[href].focus-visible,
.main a[data-goto]:hover,
.main a[data-goto]:active,
.main a[data-goto].focus-visible,
.main .text-button:hover,
.main .text-button:active,
.main .text-button.focus-visible {
  text-decoration: none;
}
@media (hover: none), (pointer: coarse) {
  .main a[href].arming,
  .main a[data-goto].arming,
  .main .text-button.arming {
    text-decoration: underline;
  }
  .main a[href].focus-visible,
  .main a[data-goto].focus-visible,
  .main .text-button.focus-visible {
    text-decoration: none;
  }
}

.content a[href],
.content a[data-goto],
.content .text-button {
  color: rgb(255, 160, 160);
}
.content a[href]:active, .content a[href].focus-visible,
.content a[data-goto]:active,
.content a[data-goto].focus-visible,
.content .text-button:active,
.content .text-button.focus-visible {
  color: rgb(255, 255, 255);
}
@media (hover: none), (pointer: coarse) {
  .content a[href].arming,
  .content a[data-goto].arming,
  .content .text-button.arming {
    color: rgb(255, 255, 255);
  }
}
.content .quote,
.content .excerpt {
  margin-left: 1.8rem;
  margin-right: 1.8rem;
  border-radius: 0.6rem;
  background-color: rgb(88, 56, 88);
}
.content .quote p,
.content .excerpt p {
  margin: 0.3rem 0;
}
.content .quote {
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  justify-content: space-between;
  padding: 0.6rem 0.6rem;
  font-style: italic;
}
.content .quote > * {
  flex-shrink: 1;
}
.content .quote::before {
  content: open-quote;
  margin-right: 0.6rem;
}
.content .quote::after {
  content: close-quote;
  margin-left: 0.6rem;
}
.content .quote::before, .content .quote::after {
  font-size: 3em;
  font-family: serif;
  opacity: 0.3;
}
.content .excerpt {
  padding: 0.6rem 1.2rem;
  font-style: normal;
}
.content .excerpt q {
  font-style: italic;
}
.content li:not(:last-child) > .quote,
.content li:not(:last-child) > .excerpt {
  margin-bottom: 0.6rem;
}

.dialogue > p {
  margin-top: 0.2em;
  margin-bottom: 0.2em;
}
.dialogue > p:first-child {
  margin-top: 0;
}
.dialogue > p:last-child {
  margin-bottom: 0;
}

.speaker {
  font-style: normal;
}

.speaker::after {
  content: ":";
}

.spoken-line {
  font-style: italic;
}
.spoken-line::before {
  content: open-quote;
}
.spoken-line::after {
  content: close-quote;
}

::selection {
  background: rgb(48, 64, 168);
}

/* Chapter tables */
.chapters {
  margin-bottom: 1.2rem;
  width: 100%;
  table-layout: fixed;
  border-collapse: collapse;
  border: none;
}
.chapters td {
  border: 1px solid rgb(128, 128, 128);
}
.chapters td:nth-of-type(odd) {
  width: 8%;
  padding: 0.3rem 0.1rem;
  text-align: center;
  font-size: 1.1em;
  font-family: Calibri;
  line-height: 1;
  color: rgb(255, 192, 192);
  word-break: break-all;
}
.chapters td:nth-of-type(1) {
  background: rgb(160, 16, 16);
}
.chapters td:nth-of-type(3) {
  background: rgb(16, 16, 160);
}
.chapters td:nth-of-type(even) {
  padding: 0.3rem 0.6rem;
  text-align: center;
  font-size: 1em;
  font-family: Calibri;
  background: rgb(64, 64, 64);
  color: rgb(224, 224, 224);
}

/* Spoilers */
.inline-spoiler {
  cursor: pointer;
}
.inline-spoiler.arming, .inline-spoiler.focus-visible {
  color: rgb(255, 255, 255);
}
.inline-spoiler:not(.show)::selection, .inline-spoiler:not(.show) *::selection {
  color: transparent;
}

/* Footer */
.footer {
  padding: 0.6rem 1.2rem;
  text-align: center;
  font: 1em/1.4 Times, serif;
  background-color: rgba(64, 64, 64, 0.9);
  color: rgb(128, 128, 208);
}
.footer a[href],
.footer a[data-goto],
.footer .text-button {
  color: rgb(255, 96, 96);
}
.footer a[href]:active, .footer a[href].focus-visible,
.footer a[data-goto]:active,
.footer a[data-goto].focus-visible,
.footer .text-button:active,
.footer .text-button.focus-visible {
  color: rgb(255, 255, 255);
}
@media (hover: none), (pointer: coarse) {
  .footer a[href].arming,
  .footer a[data-goto].arming,
  .footer .text-button.arming {
    color: rgb(255, 255, 255);
  }
}

/* Collapsible elements */
.collapsible {
  position: relative;
}
.collapsible > .collapse-toggle {
  position: relative;
  box-sizing: border-box;
  width: 100%;
  cursor: pointer;
  user-select: none;
}
.panel-major.collapsible > .collapse-toggle {
  color: rgb(255, 136, 136);
  outline: none;
  padding-right: 3.6rem;
}
.panel-major.collapsible > .collapse-toggle:hover, .panel-major.collapsible > .collapse-toggle.focus-visible {
  color: rgb(255, 255, 255);
}

@media (hover: none), (pointer: coarse) {
  .panel-major.collapsible > .collapse-toggle:hover {
    color: rgb(255, 136, 136);
  }
  .panel-major.collapsible > .collapse-toggle:active {
    color: rgb(255, 255, 255);
  }
}

.panel-minor.collapsible > .collapse-toggle,
.submenu.collapsible > .collapse-toggle {
  display: flex;
  flex-flow: row nowrap;
  justify-content: flex-start;
  align-items: center;
}

.panel-minor.collapsible > .collapse-toggle {
  color: rgb(255, 160, 160);
  outline-offset: 0.3em;
}
.panel-minor.collapsible > .collapse-toggle:hover, .panel-minor.collapsible > .collapse-toggle.focus-visible {
  color: rgb(255, 255, 255);
}

@media (hover: none), (pointer: coarse) {
  .panel-minor.collapsible > .collapse-toggle:hover {
    color: rgb(255, 160, 160);
  }
  .panel-minor.collapsible > .collapse-toggle:active {
    color: rgb(255, 255, 255);
  }
}
.show.panel-minor.collapsible > .collapse-toggle ~ .collapse-content {
  color: inherit;
  outline: none;
}

.show.panel-minor.collapsible > .collapse-toggle:hover ~ .collapse-content, .show.panel-minor.collapsible > .collapse-toggle.focus-visible ~ .collapse-content {
  color: rgb(255, 255, 255);
  outline: 1px auto rgb(34, 34, 34);
}

@media (hover: none), (pointer: coarse) {
  .show.panel-minor.collapsible > .collapse-toggle:hover ~ .collapse-content {
    color: rgb(255, 255, 255);
    outline: 1px auto rgb(34, 34, 34);
  }
  .show.panel-minor.collapsible > .collapse-toggle:active ~ .collapse-content {
    color: rgb(255, 255, 255);
    outline: 1px auto rgb(34, 34, 34);
  }
}
.panel-minor.collapsible > .collapse-toggle.focus-visible {
  outline: 1px auto rgb(34, 34, 34);
}
.panel-minor.collapsible > .collapse-toggle:not(.noprefix) > div::before {
  content: "Show ";
}
.show.panel-minor.collapsible > .collapse-toggle:not(.noprefix) > div::before {
  content: "Hide ";
}

.submenu.collapsible > .collapse-toggle {
  color: rgb(255, 128, 128);
  outline: none;
}
.submenu.collapsible > .collapse-toggle:hover, .submenu.collapsible > .collapse-toggle.focus-visible {
  color: rgb(255, 255, 255);
}

@media (hover: none), (pointer: coarse) {
  .submenu.collapsible > .collapse-toggle:hover {
    color: rgb(255, 128, 128);
  }
  .submenu.collapsible > .collapse-toggle:active {
    color: rgb(255, 255, 255);
  }
}
.submenu-main.submenu.collapsible > .collapse-toggle {
  justify-content: center;
}

.panel-minor.collapsible > .collapse-toggle > div {
  display: inline-block;
  vertical-align: middle;
  flex-grow: 0;
  flex-shrink: 1;
}

.collapsible > .collapse-toggle::after {
  width: 0.8em;
  height: 0.8em;
  background-image: url(../img/icons/toggle.svg);
  background-repeat: no-repeat;
  background-position: 0 0;
  background-size: 1.6em 1.6em;
  content: "";
}
.panel-major.collapsible > .collapse-toggle::after {
  position: absolute;
  top: 50%;
  right: 1.2rem;
  transform: translate(0, -50%);
}

.panel-minor.collapsible > .collapse-toggle::after,
.submenu.collapsible > .collapse-toggle::after {
  display: block;
  margin-left: 0.5em;
  flex-shrink: 0;
}

.show.collapsible > .collapse-toggle::after {
  background-position-x: -0.8em;
}

.collapsible > .collapse-toggle::after {
  background-position-y: 0;
}
.collapsible > .collapse-toggle:hover::after, .collapsible > .collapse-toggle.focus-visible::after {
  background-position-y: -0.8em;
}

@media (hover: none), (pointer: coarse) {
  .collapsible > .collapse-toggle:hover::after {
    background-position-y: 0;
  }
  .collapsible > .collapse-toggle:active::after {
    background-position-y: -0.8em;
  }
}
.panel-minor.collapsible > .collapse-content {
  outline-offset: 0.3em;
}

/* Header overlay */
.header-overlay {
  z-index: 5;
}

/* Tooltips */
.tippy-box {
  text-align: center;
  border-radius: 0.5rem;
  font: 1rem/1.4 Arial, sans-serif;
  background-color: rgb(255, 96, 96);
  color: rgb(255, 255, 255);
  user-select: none;
}

.tippy-arrow {
  color: rgb(255, 96, 96);
}

#tooltip_fixed_root {
  position: fixed;
  top: 0;
  right: 0;
  width: 100%;
  height: 0;
  overflow: visible;
  z-index: 9999;
}

/* Print */
@media print {
  /* Hiding interactive elements */
  #navbar_container,
  #menu_container,
  .tippy-box,
  .header-overlay {
    display: none !important;
  }
  .collapsible > .collapse-toggle::after {
    display: none !important;
  }
  .panel-minor.collapsible > .collapse-toggle > *::before {
    display: none !important;
  }
  .show.panel-minor.collapsible > .collapse-toggle {
    display: none !important;
  }
  :not(.show).panel-minor.collapsible > .collapse-toggle > *::first-letter {
    text-transform: uppercase;
  }
  :not(.show).panel-minor.collapsible > .collapse-toggle > *::after {
    content: " (hidden)";
  }
  .footer {
    display: none !important;
  }
  /* Printer-friendly graphics */
  body {
    background: transparent !important;
  }
  .header {
    background: transparent !important;
  }
  .title {
    color: rgb(255, 0, 0) !important;
  }
  .content {
    background: transparent !important;
  }
  .panel-major > .panel-heading {
    background: transparent !important;
    color: rgb(255, 0, 0) !important;
    padding-top: 0.2rem !important;
    padding-bottom: 0.2rem !important;
  }
  .panel-major > .panel-body {
    color: rgb(0, 0, 0) !important;
  }
  .panel-minor > .panel-heading {
    color: rgb(224, 0, 0) !important;
  }
  .chapters * {
    background: rgb(240, 240, 240) !important;
    color: rgb(0, 0, 0) !important;
    border-color: rgb(0, 0, 0) !important;
  }
  .chapters td:nth-of-type(odd) {
    background: rgb(208, 208, 208) !important;
  }
  a {
    color: inherit !important;
    text-decoration: none !important;
  }
  a[href]::after {
    content: " [" attr(href) "]";
  }
  .quote,
  .excerpt {
    background: rgb(208, 208, 208) !important;
  }
  .quote::before,
  .quote::after {
    opacity: 1 !important;
  }
  .inline-spoiler {
    color: inherit !important;
  }
  .inline-spoiler:not(.show) {
    font-size: 0 !important;
    color: rgb(255, 64, 64) !important;
  }
  .inline-spoiler:not(.show)::after {
    font-size: 1rem;
    content: "[hidden: " attr(data-spoiler) "]";
  }
  /* Structure */
  .main {
    width: 100% !important;
  }
  .content {
    column-width: 520px;
    column-gap: 0;
  }
  /* Fonts */
  :root {
    font-size: 11pt !important;
  }
  .title {
    font: bold italic 2em/1.4 Times, serif !important;
  }
  .content {
    font: 1em/1.4 Times, serif !important;
  }
  .chapters td:nth-of-type(odd) {
    font-family: Calibri !important;
  }
  .chapters td:nth-of-type(even) {
    font-family: Times, serif !important;
  }
}